
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  margin-top: 60px;
}
a {
  color: white;
}
body {
  padding: 0;
  margin: 0;
  background-color: black;
}
canvas {
  vertical-align: top;
}
:-webkit-full-screen {
  background-color: black;
}
:-moz-full-screen {
  background-color: black;
}
:-ms-full-screen {
  background-color: black;
}
:-o-full-screen {
  background-color: black;
}
:full-screen {
  background-color: black;
}



